import axios from 'axios';

export const updateHouseAvailability = async (selectedDates, isHouseUnavailable) => {
  try {
    const formattedDates = selectedDates.map((date) => date);
    await axios.post('http://localhost:9000/update_availability', {
      dates: formattedDates,
      isUnavailable: isHouseUnavailable,
    });
    return true; // Mise  jour russie
  } catch (error) {
    console.error('Erreur lors de la mise  jour de la disponibilit :', error);
    return false; // Erreur lors de la mise  jour
  }
};

export const deleteHouseAvailability = async (date) => {
  try {
    await axios.post('http://localhost:9000/delete_availability', { date });
    return true; // Suppression russie
  } catch (error) {
    console.error('Erreur lors de la suppression de la disponibilit :', error);
    return false; // Erreur lors de la suppression
  }
};

export const deleteAllHouseAvailability = async () => {
  try {
    await axios.post('http://localhost:9000/delete_all_availability');
    return true; // Suppression de toutes les disponibilits russie
  } catch (error) {
    console.error('Erreur lors de la suppression de toutes les disponibilits :', error);
    return false; // Erreur lors de la suppression
  }
};
